img {
  max-width: 100%; }

*,
::after,
::before {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

.container {
  width: 900px;
  margin: 0 auto; }
  @media screen and (min-width: 1500px) {
    .container {
      width: 1200px;
      margin: 0 auto; } }
  @media screen and (max-width: 1023px) {
    .container {
      width: 100%;
      padding: 0 35px; } }
  @media screen and (max-width: 767px) {
