.page {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: "Open Sans", "Arial", sans-serif;
  height: 100%; }

.page body {
  margin: 0;
  min-height: 100%;
  display: grid;
  grid-template-rows: -webkit-min-content 1fr -webkit-min-content;
  grid-template-rows: min-content 1fr min-content;
  -ms-flex-line-pack: start;
      align-content: start;
  background-color: #ccc6c6; }

.header h1 {
  text-align: center;
  padding: 25px 0; }

.calc {
  display: grid;
  grid-template-columns: 50px 50px 50px 50px;
  grid-template-rows: 50px 50px 50px 50px 50px 50px;
  gap: 25px;
  width: 325px;
  background: #ffffff;
  padding: 22px;
  border-radius: 5px; }

.calc__output {
  grid-column: 1 / -1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  background: #7a7474;
  border-radius: 5px;
  padding: 0 15px;
  color: #ffffff;
  font-size: 18px;
  letter-spacing: 1px; }

.calc__button {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  border-radius: 5px;
  -webkit-box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
          box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
  background: none;
  border: none;
  cursor: pointer;
  font-size: 16px;
  font-weight: bold; }

.calc__button_equal {
  grid-column: 1 / -1;
  background: #f59d18; }
